import "./styles.css";


  // e.preventDefault();
  
  function abc(e){
    e.preventDefault();
    const form =document.querySelector("#new-task-form");
  const input =document.querySelector("#new-task-input");
  const list =document.querySelector("#tasks");
    const input1=input.value;
      // console.log(input1);
    const taskele=document.createElement("div");
    taskele.classList.add("task");
    const taskeleContent=document.createElement("div");
    taskeleContent.classList.add("content");
    
    const taskinput=document.createElement("input");
    taskinput.classList.add("text");
    taskinput.type="text";
    taskinput.value=input1;
    taskinput.setAttribute('readonly','readonly');
    taskeleContent.appendChild(taskinput);
    
    const taskaction=document.createElement("div");
    taskaction.classList.add("actions");
    
      const taskedit=document.createElement("button");
    taskedit.classList.add("edit");
    taskedit.innerHTML="Edit";
    
          const taskdelete=document.createElement("button");
    taskdelete.classList.add("delete");
    taskdelete.innerHTML="Delete";
   
   taskaction.appendChild(taskedit);
    taskaction.appendChild(taskdelete);
    
     taskele.appendChild(taskeleContent);
     taskele.appendChild(taskaction);

    list.appendChild(taskele);
    
    input.value=" ";
    
    
    taskedit.addEventListener("click",function(){
      if(taskedit.innerText.toLowerCase()=="edit"){
        taskinput.removeAttribute("readonly");
        taskinput.focus();
        taskedit.innerHTML="Save";
      }
       else {
         taskinput.setAttribute("readonly","readonly");
         taskedit.innerHTML="Edit";
       }   
    });

    taskdelete.addEventListener("click",function(){
      const a= confirm("Are you sure you want to delete");
      if(a==true){
      list.removeChild(taskele);
      }
    }); 
    
  }
  





export default function App() {
  return (
    <div className="App">
      <header>
		<h1>Todo List 2023</h1>
		<form id="new-task-form">
			<input 
				type="text" 
				name="new-task-input" 
				id="new-task-input" 
				placeholder="What do you have planned?" />
      <button 
				type="submit"
				id="new-task-submit" onClick={abc}>Add task</button>
		</form>
	</header>
	<main>
		<section class="task-list">
			<h2>Tasks</h2>

			<div id="tasks">

			</div>
		</section>
	</main>
    </div>
  );
}
